.o-radio {
  display: block;
  position: relative;
  padding-left: 35px;
  margin: 0 0 0 35px;
  cursor: pointer;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none; }

.o-radio input {
  position: absolute;
  opacity: 0;
  cursor: pointer; }

.checkmark {
  position: absolute;
  top: 0;
  left: 0;
  height: 25px;
  width: 25px;
  background-color: #eee;
  border-radius: 50%; }

.o-radio:hover input ~ .checkmark {
  background-color: #ccc; }

.o-radio input:checked ~ .checkmark {
  background-color: #4099da; }

.o-radio input:checked ~ .checkmark:after {
  display: block; }

.checkmark:after {
  content: "";
  position: absolute;
  display: none; }

.o-radio .checkmark:after {
  top: 9px;
  left: 9px;
  width: 8px;
  height: 8px;
  border-radius: 50%;
  background: white; }

